import React, { useState } from 'react'
import Helmet from 'react-helmet'
import Header from './header'
import Footer from './footer'
import GlobalStyles from '../utils/styles/global'
import { useKeyboardUser } from '../utils/hooks'
import { joinNonEmpty } from '../utils/array'

const Layout = ({ children }) => {
  const isKeyboardUser = useKeyboardUser()
  const [showMobileMenu, setShowMobileMenu] = useState(false)
  const toggleMobileMenu = () => setShowMobileMenu(!showMobileMenu)
  const bodyClassNames = [
    showMobileMenu ? 'mobile-menu-is-active' : '',
    isKeyboardUser ? 'keyboardUser' : ''
  ]

  return (
    <React.Fragment>
      <GlobalStyles />
      <Helmet
        bodyAttributes={{
          class: joinNonEmpty(bodyClassNames, ' ')
        }}
      >
        <html lang="en" />
        <title>Home | Zachary McNellis</title>
        <link rel="apple-touch-icon" sizes="57x57" href="/apple-icon-57x57.png" />
        <link rel="apple-touch-icon" sizes="60x60" href="/apple-icon-60x60.png"/>
        <link rel="apple-touch-icon" sizes="72x72" href="/apple-icon-72x72.png"/>
        <link rel="apple-touch-icon" sizes="76x76" href="/apple-icon-76x76.png"/>
        <link rel="apple-touch-icon" sizes="114x114" href="/apple-icon-114x114.png"/>
        <link rel="apple-touch-icon" sizes="120x120" href="/apple-icon-120x120.png"/>
        <link rel="apple-touch-icon" sizes="144x144" href="/apple-icon-144x144.png"/>
        <link rel="apple-touch-icon" sizes="152x152" href="/apple-icon-152x152.png"/>
        <link rel="apple-touch-icon" sizes="180x180" href="/apple-icon-180x180.png"/>
        <link rel="icon" type="image/png" sizes="192x192"  href="/android-icon-192x192.png"/>
        <link rel="icon" type="image/png" sizes="32x32" href="/favicon-32x32.png"/>
        <link rel="icon" type="image/png" sizes="96x96" href="/favicon-96x96.png"/>
        <link rel="icon" type="image/png" sizes="16x16" href="/favicon-16x16.png"/>
        <link rel="manifest" href="/manifest.json" />
        <meta name="msapplication-TileColor" content="#ffffff" />
        <meta name="msapplication-TileImage" content="/ms-icon-144x144.png"/>
        <meta name="theme-color" content="#000000" />
      </Helmet>
      <Header
        showMobileMenu={showMobileMenu}
        onMobileMenuClick={toggleMobileMenu}
      />
      {children}
      <Footer />
    </React.Fragment>
  )
}

export default Layout
